import React, { useEffect, useMemo, useState } from "react";
import Breadcrumb from "../shared/Breadcrumb";
import { i18n } from "../../i18n";
import { AiOutlineCalendar } from "react-icons/ai";
import { MdLocationOn } from "react-icons/md";
import Image from "../shared/Image";
import { noImage } from "../../assets/images";
import { useDispatch, useSelector } from "react-redux";
import action from "src/modules/evenement/view/evenementViewActions";
import { useRouteMatch } from "react-router-dom";
import selector from "src/modules/evenement/view/evenementViewSelectors";
import Date from "../shared/Date";
import Youtube from "../shared/Youtube";
import "leaflet/dist/leaflet.css";
import EvenementAvenir from "./EvenementAvenir";
import ImageModal from "../shared/modals/ImageModal";
import Translate from "../shared/Translate";
import {
  FacebookShareButton,
  FacebookIcon,
  TwitterShareButton,
  TwitterIcon,
  LinkedinShareButton,
  LinkedinIcon,
} from "react-share";
import { Carousel, Tooltip } from "antd";
import authSelectors from "src/modules/auth/authSelectors";
import { MapContainer, TileLayer, Marker, Popup } from "react-leaflet";
import icon from "leaflet/dist/images/marker-icon.png";
import iconShadow from "leaflet/dist/images/marker-shadow.png";
import { StarOutlined, StarFilled } from "@ant-design/icons";
import AuthService from "src/modules/auth/authService";

import L from "leaflet";
import MyPDFViewer from "src/view/shared/show-one/components/MyPDFViewer";

import { RightOutlined, LeftOutlined } from "@ant-design/icons";
function EvenementDetaill() {
  const dispatch = useDispatch();
  const match = useRouteMatch();
  const selectRows = useSelector(selector.selectRecord);
  const selectLoading = useSelector(selector.selectLoading);
  const [image, setImage] = useState<any>(null);
  const langue = localStorage.getItem("language");
  const id = match.params.id;
  const currentUser = useSelector(authSelectors.selectCurrentUser);
  const [isFav, setIsFav] = useState(false);

  const SampleNextArrow = (props) => {
    const { className, style, onClick } = props;
    return (
      <RightOutlined
        className={className}
        style={{
          // ...style,
          color: "#e1011a",
          fontSize: "25px",
          lineHeight: "1.5715",
          right: -5,
          zIndex: 99999,
        }}
        onClick={onClick}
      />
    );
  };

  const SamplePrevArrow = (props) => {
    const { className, style, onClick } = props;
    return (
      <LeftOutlined
        className={className}
        style={{
          // ...style,
          color: "#e1011a",
          fontSize: "25px",
          lineHeight: "1.5715",
          left: -5,
          zIndex: 99999,
        }}
        onClick={onClick}
      />
    );
  };

  const settings = {
    nextArrow: <SampleNextArrow />,
    prevArrow: <SamplePrevArrow />,
  };

  useEffect(() => {
    dispatch(action.doFind(id));
  }, [dispatch, id]);

  let DefaultIcon = L.icon({
    iconUrl: icon,
    shadowUrl: iconShadow,
  });

  L.Marker.prototype.options.icon = DefaultIcon;
  const doPreviewImage = (image) => {
    setImage({
      src: image.downloadUrl,
      alt: image.name,
    });
  };

  const doCloseImageModal = () => {
    setImage(null);
  };
  const onChange = (currentSlide: number) => {};
  useEffect(() => {
    if (currentUser && currentUser.favorites) {
      currentUser.favorites.evenement?.map((event) => {
        if (event.id === selectRows?.id) {
          setIsFav(true);
        }
      });
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [currentUser, selectRows]);
  const addToFavourite = async (id) => {
    currentUser.favorites.evenement.push(id);
    const data = {
      ...currentUser,
    };
    await AuthService.updateProfile(data);
    setIsFav(true);
  };

  const popFromFavourite = async (id) => {
    var index = 0;
    currentUser.favorites.evenement.map((event, eventIndex) => {
      if (event.id === id) {
        index = eventIndex;
        return;
      }
    });
    currentUser.favorites.evenement.splice(index, 1);
    const data = {
      ...currentUser,
    };
    await AuthService.updateProfile(data);
    setIsFav(false);
  };
  return (
    <>
      {selectLoading && <h1 className="h1">{i18n("table.loading")}</h1>}
      {!selectLoading && (
        <>
          {" "}
          <Breadcrumb
            items={[
              [i18n("app.title"), "/"],
              [i18n("common.EvÃ¨nements"), "/evenement"],
            ]}
          />
          <div className="page__evenement">
            <div className="evenment__header">
              {langue === "fr" ? (
                <>
                  <div style={{ position: "relative" }}>
                    {currentUser !== null ? (
                      <div
                        style={{
                          position: "absolute",
                          top: 0,
                          right: 0,
                          zIndex: 2,
                          padding: 10,
                        }}
                      >
                        {isFav ? (
                          <Tooltip title={i18n("common.unfavoris")}>
                            <StarFilled
                              style={{ color: "#e1011a", fontSize: 35 }}
                              onClick={() => popFromFavourite(selectRows.id)}
                            />
                          </Tooltip>
                        ) : (
                          <Tooltip title={i18n("common.favoris")}>
                            <StarOutlined
                              style={{ color: "#e1011a", fontSize: 35 }}
                              onClick={() => addToFavourite(selectRows.id)}
                            />
                          </Tooltip>
                        )}
                      </div>
                    ) : null}
                    <Image
                      width={1170}
                      height={404}
                      src={
                        selectRows?.photos?.length > 0
                          ? selectRows?.photos[0]?.downloadUrl
                          : noImage
                      }
                      alt="Placeholder"
                      style={{ position: "absolute", top: 0, left: 0 }}
                    />
                  </div>
                </>
              ) : langue === "ar" ? (
                <>
                  <div style={{ position: "relative" }}>
                    {currentUser !== null ? (
                      <div
                        style={{
                          position: "absolute",
                          top: 0,
                          right: 0,
                          zIndex: 2,
                          padding: 10,
                        }}
                      >
                        {isFav ? (
                          <Tooltip title={i18n("common.unfavoris")}>
                            <StarFilled
                              style={{ color: "#e1011a", fontSize: 35 }}
                              onClick={() => popFromFavourite(selectRows.id)}
                            />
                          </Tooltip>
                        ) : (
                          <Tooltip title={i18n("common.favoris")}>
                            <StarOutlined
                              style={{ color: "#e1011a", fontSize: 35 }}
                              onClick={() => addToFavourite(selectRows.id)}
                            />
                          </Tooltip>
                        )}
                      </div>
                    ) : null}
                    <Image
                      width={1170}
                      height={404}
                      src={
                        selectRows?.photosAR?.length > 0
                          ? selectRows?.photosAR[0]?.downloadUrl
                          : noImage
                      }
                      alt="Placeholder"
                      style={{ position: "absolute", top: 0, left: 0 }}
                    />
                  </div>
                </>
              ) : (
                <>
                  <div style={{ position: "relative" }}>
                    {currentUser !== null ? (
                      <div
                        style={{
                          position: "absolute",
                          top: 0,
                          right: 0,
                          zIndex: 2,
                          padding: 10,
                        }}
                      >
                        {isFav ? (
                          <Tooltip title={i18n("common.unfavoris")}>
                            <StarFilled
                              style={{ color: "#e1011a", fontSize: 35 }}
                              onClick={() => popFromFavourite(selectRows.id)}
                            />
                          </Tooltip>
                        ) : (
                          <Tooltip title={i18n("common.favoris")}>
                            <StarOutlined
                              style={{ color: "#e1011a", fontSize: 35 }}
                              onClick={() => addToFavourite(selectRows.id)}
                            />
                          </Tooltip>
                        )}
                      </div>
                    ) : null}
                    <Image
                      width={1170}
                      height={404}
                      src={
                        selectRows?.photosEN?.length > 0
                          ? selectRows?.photosEN[0]?.downloadUrl
                          : noImage
                      }
                      alt="Placeholder"
                      style={{ position: "absolute", top: 0, left: 0 }}
                    />
                  </div>
                </>
              )}

              <div className="evenement__message">
                <div className="messageEvenement__left">
                  <div className="left__number">
                    {Date.Day(selectRows?.date)}
                  </div>
                  <div className="left__months">
                    {Date.Month(selectRows?.date)}
                  </div>
                </div>
                <div className="messageEvenement__right">
                  <div className="messageEvenement__title">
                    {Translate.Trans("title", selectRows)}
                  </div>
                  <div className="messageEvenement__description">
                    <div>
                      <AiOutlineCalendar /> {Date.date(selectRows?.date)}
                    </div>
                    <div>
                      <MdLocationOn />
                      {Translate.TransEmplacement(selectRows)}
                    </div>
                  </div>
                </div>
              </div>
            </div>

            <div className="detaill__evenement">
              <div className="detaillEvenement__left">
                <div className="left__description">
                  <div className="title__detaillEvenemet">
                    {i18n("common.Description")}
                  </div>
                  <div
                    className="description__detaillEvenement"
                    dangerouslySetInnerHTML={{
                      __html: Translate.Trans("description", selectRows),
                    }}
                  ></div>
                </div>
                {selectRows?.date && (
                  <div className="left__date">
                    <div className="title__detaillEvenemet">
                      {i18n("common.Date")}
                    </div>
                    <div className="description__detaillEvenement">
                      <b> {Date.DetaillDate(selectRows?.date)} </b> <br />
                      {i18n("common.horraire")} :{" "}
                      <b>{Date.Hour(selectRows?.date)}</b>
                    </div>
                  </div>
                )}

                <div className="left__photos">
                  <div className="title__detaillEvenemet">
                    {i18n("common.Photos")}
                  </div>
                  <div className="detail__description">
                    {langue === "fr" ? (
                      <>
                        {selectRows?.photos?.length > 0 ? (
                          <Carousel
                            arrows={true}
                            {...settings}
                            autoplay
                            afterChange={onChange}
                          >
                            {selectRows?.photos?.map((item) => (
                              <div
                                style={{ paddingTop: "19px" }}
                                onClick={() => doPreviewImage(item)}
                              >
                                <Image
                                  width={"100%"}
                                  height={"500"}
                                  src={item.downloadUrl}
                                />
                              </div>
                            ))}
                          </Carousel>
                        ) : null}
                      </>
                    ) : langue === "ar" ? (
                      <>
                        {selectRows?.photosAR?.length > 0 ? (
                          <Carousel autoplay afterChange={onChange}>
                            {selectRows?.photosAR?.map((item) => (
                              <div
                                style={{ paddingTop: "19px" }}
                                onClick={() => doPreviewImage(item)}
                              >
                                <Image
                                  width={"100%"}
                                  height={"500"}
                                  src={item.downloadUrl}
                                />
                              </div>
                            ))}
                          </Carousel>
                        ) : null}
                      </>
                    ) : (
                      <>
                        {selectRows?.photosEN?.length > 0 ? (
                          <Carousel autoplay afterChange={onChange}>
                            {selectRows?.photosEN?.map((item) => (
                              <div
                                style={{ paddingTop: "19px" }}
                                onClick={() => doPreviewImage(item)}
                              >
                                <Image
                                  width={"100%"}
                                  height={"500"}
                                  src={item.downloadUrl}
                                />
                              </div>
                            ))}
                          </Carousel>
                        ) : null}
                      </>
                    )}
                  </div>

                  {image && (
                    <ImageModal
                      src={image.src}
                      alt={image.alt}
                      onClose={() => doCloseImageModal()}
                    />
                  )}
                </div>
                {selectRows?.videos.length > 0 ? (
                  <>
                    {selectRows?.videos[0]?.link && (
                      <div className="left__videos">
                        <div className="title__detaillEvenemet">
                          {i18n("common.Videos")}
                        </div>
                        <div className="photos__gallery">
                          {selectRows?.videos?.map(
                            (item) =>
                              item?.link && (
                                <div
                                  className="videos__list"
                                  style={{ marginBottom: 10 }}
                                >
                                  <Youtube
                                    link={item?.link}
                                    width={717}
                                    height={300}
                                  />
                                </div>
                              )
                          )}
                        </div>
                      </div>
                    )}
                  </>
                ) : null}
              </div>
              <div className="detailEvenement__right">
                {/* Dossier presse */}
                {currentUser &&
                currentUser.tenants[0].roles.includes("journalist") ? (
                  <>
                    {selectRows?.contactPresse && (
                      <div className="left__contact">
                        <div className="title__detaillEvenemet">
                          {i18n("common.contactPresse")}
                        </div>
                        <div
                          className="description__detaillEvenement"
                          style={{ display: "flex" }}
                        >
                          {i18n("common.Contact")} :{" "}
                          <div
                            style={{
                              fontSize: 15,
                              fontWeight: "bold",
                            }}
                          >
                            {selectRows?.contactPresse}
                          </div>
                        </div>
                      </div>
                    )}
                    {selectRows?.dossierPresse.length > 0 && (
                      <a
                        href={selectRows?.dossierPresse[0]?.downloadUrl}
                        download
                        style={{
                          color: "#fff",
                          fontSize: 20,
                          border: "solid",
                          borderRadius: "5px",
                          backgroundColor: "#e1011a",
                          borderColor: "#e1011a",
                        }}
                      >
                        {i18n("telecharger")}
                      </a>
                    )}
                  </>
                ) : null}
                {selectRows?.localisation ? (
                  <>
                    <div className="title__detaillEvenemet">
                      {i18n("common.Lieu")}
                    </div>
                    <div>
                      <MapContainer
                        center={[
                          parseFloat(selectRows?.localisation.split(",")[0]),
                          parseFloat(selectRows?.localisation.split(",")[1]),
                        ]}
                        zoom={10}
                        scrollWheelZoom={false}
                        className="map"
                      >
                        <TileLayer
                          attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
                          url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
                        />
                        <Marker
                          position={[
                            parseFloat(selectRows?.localisation.split(",")[0]),
                            parseFloat(selectRows?.localisation.split(",")[1]),
                          ]}
                        ></Marker>
                      </MapContainer>
                    </div>
                  </>
                ) : null}

                {selectRows?.contact1 && (
                  <div className="left__contact">
                    <div className="title__detaillEvenemet">
                      {i18n("common.Contact")}
                    </div>
                    <div
                      className="description__detaillEvenement"
                      dangerouslySetInnerHTML={{
                        __html: selectRows?.contact1,
                      }}
                    ></div>
                  </div>
                )}

                <div className="detaillEvenement__socialMedia">
                  <div className="socialMedia__title">
                    {i18n("common.Partager")} :
                  </div>
                  <div>
                    <FacebookShareButton url={window.location.href}>
                      <FacebookIcon size={35} round />
                    </FacebookShareButton>
                  </div>

                  <div>
                    <TwitterShareButton url={window.location.href}>
                      <TwitterIcon size={35} round />
                    </TwitterShareButton>
                  </div>
                  <div>
                    <LinkedinShareButton url={window.location.href}>
                      <LinkedinIcon size={35} round />
                    </LinkedinShareButton>
                  </div>
                </div>
              </div>
              <div className="App">
                {langue === "fr" ? (
                  <>
                    {" "}
                    {selectRows?.supports.length > 0 ? (
                      <div>
                        <>
                          {selectRows.supports[0].name.split(".").pop() ===
                          "pdf" ? (
                            <MyPDFViewer
                              pdf={selectRows?.supports[0]?.downloadUrl}
                            />
                          ) : null}
                        </>

                        <a
                          href={selectRows?.supports[0].downloadUrl}
                          download
                          style={{
                            color: "#fff",
                            fontSize: 20,
                            border: "solid",
                            borderRadius: "5px",
                            backgroundColor: "#e1011a",
                            borderColor: "#e1011a",
                          }}
                        >
                          {i18n("telecharger")}
                        </a>
                        <hr />
                      </div>
                    ) : null}
                  </>
                ) : langue === "ar" ? (
                  <>
                    {" "}
                    {selectRows?.supportsAR.length > 0 ? (
                      <div>
                        <>
                          {selectRows.supportsAR[0].name.split(".").pop() ===
                          "pdf" ? (
                            <MyPDFViewer
                              pdf={selectRows?.supportsAR[0]?.downloadUrl}
                            />
                          ) : null}
                        </>

                        <a
                          href={selectRows?.supportsAR[0].downloadUrl}
                          download
                          style={{
                            color: "#fff",
                            fontSize: 20,
                            border: "solid",
                            borderRadius: "5px",
                            backgroundColor: "#e1011a",
                            borderColor: "#e1011a",
                          }}
                        >
                          {i18n("telecharger")}
                        </a>
                        <hr />
                      </div>
                    ) : null}
                  </>
                ) : (
                  <>
                    {" "}
                    {selectRows?.supportsEN.length > 0 ? (
                      <div>
                        <>
                          {selectRows.supportsEN[0].name.split(".").pop() ===
                          "pdf" ? (
                            <MyPDFViewer
                              pdf={selectRows?.supportsEN[0]?.downloadUrl}
                            />
                          ) : null}
                        </>

                        <a
                          href={selectRows?.supportsEN[0].downloadUrl}
                          download
                          style={{
                            color: "#fff",
                            fontSize: 20,
                            border: "solid",
                            borderRadius: "5px",
                            backgroundColor: "#e1011a",
                            borderColor: "#e1011a",
                          }}
                        >
                          {i18n("telecharger")}
                        </a>
                        <hr />
                      </div>
                    ) : null}
                  </>
                )}
              </div>
            </div>
            <EvenementAvenir />
          </div>
        </>
      )}
    </>
  );
}

export default EvenementDetaill;
